import { users, getOpenid, get_phoneLogin } from '@/http/api.js';
export default {
	data() {
		return {
			id: '',
			isDis: '',
			uid: '',
			isShare: 1, // 1:普通分享   2:普通分销   3:我要分销
			needUserInfo_active: 0,
			needToken_active: 0,
			data: '',
			code: '',
			userInfo: '',
			openid: '',
			session_key: '',
			token: '',
			needUserInfo: 0,
			needToken: 0,
			phones: 0,
			datas: ''
			// openidUrl: 'https://api.weixin.qq.com/sns/jscode2session', //获取openid接口
			// APP_SECRET: 'ddec28ff357d206e32a42b41575de883',
			// APP_ID: 'wx620e699d34566854'
		};
	},
	onShow() {},
	onLoad(options) {
		this.id = options.id;
		this.isDis = options.isDis;
		this.uid = options.uid;
		if (options.isShare) {
			this.isShare = options.isShare;
		}
		if (options.needUserInfo) {
			this.needUserInfo = options.needUserInfo;
		}
		if (options.needToken) {
			this.needToken = options.needToken;
		}
		if (options.datas) {
			this.datas = options.datas;
		}
		const pages = getCurrentPages();
		const currPage = pages[pages.length - 2]; // 当前页
		console.log(currPage.data);
		if (currPage.data.id != '') {
			this.id = currPage.data.id;
		}
		if (currPage.data.isDis) {
			this.isDis = currPage.data.isDis;
		}
		if (currPage.data.uid) {
			this.uid = currPage.data.uid;
		}
		if (currPage.data.isShare) {
			this.isShare = currPage.data.isShare;
		}
		wx.getSetting({
			success: res => {
				//判断是否授权，如果授权成功
				if (res.authSetting['scope.userInfo']) {
					//获取用户信息
					wx.getUserInfo({
						success: res => {
							this.userInfo = res.userInfo;
							uni.setStorageSync('userInfo', res.userInfo);
							this.needUserInfo = 0;
						}
					});
				}
			}
		});
	},
	methods: {
		//获取用户信息
		getConfiguser(e) {
			let auth_param = e.detail;
			let that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					that.code = loginRes.code;
					uni.setStorageSync('code', loginRes.code);
					getOpenid(loginRes.code).then(res => {
						if (res.code == 0) {
							that.openid = res.data.openid;
							uni.setStorageSync('openid', res.data.openid);
							that.session_key = res.data.session_key;
							uni.setStorageSync('session_key', res.data.session_key);
							uni.getSetting({
								success(res) {
									if (res.authSetting['scope.userInfo']) {
										uni.getUserInfo({
											success: function(infoRes) {
												that.userInfo = infoRes.userInfo;
												uni.setStorageSync('userInfo', infoRes.userInfo);
												that.needUserInfo_active = 1;
												//需要用户信息并且用户已经授权并且不需要授权手机号
												if (that.needUserInfo == 1 && that.needUserInfo_active == 1 && that.needToken == 0) {
													let pages = getCurrentPages();
													let prevPage = pages[pages.length - 2]; //上一个页面
													console.log(prevPage);
													//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
													prevPage.setData({
														id: that.id,
														isDis: that.isDis,
														uid: that.uid,
														isShare: that.isShare
													});
													wx.navigateBack({
														//返回
														delta: 1
													});
												} else if (that.needToken_active == 1 && that.needToken_active == 1 && that.needUserInfo == 1 && that.needUserInfo_active == 1) {
													//需要用户信息并且用户已经授权并且需要授权手机号并且手机号已经授权
													let pages = getCurrentPages();
													let prevPage = pages[pages.length - 2]; //上一个页面
													console.log(prevPage);
													//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
													prevPage.setData({
														id: that.id,
														isDis: that.isDis,
														uid: that.uid,
														isShare: that.isShare
													});
													wx.navigateBack({
														//返回
														delta: 1
													});
												}
											}
										});
									}
								}
							});
						}
					});
				}
			});
		},
		//用户授权手机号
		getConfig(e) {
			console.log(e);
			// return false;
			let auth_param = e.detail;
			let that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log(loginRes);
					that.code = loginRes.code;
					uni.setStorageSync('code', loginRes.code);
					getOpenid(loginRes.code).then(res => {
						if (res.code == 0) {
							that.openid = res.data.openid;
							uni.setStorageSync('openid', res.data.openid);
							that.session_key = res.data.session_key;
							uni.setStorageSync('session_key', res.data.session_key);
							get_phoneLogin({
								code: loginRes.code,
								encryptedData: auth_param.encryptedData,
								iv: auth_param.iv,
								openid: that.openid,
								session_key: that.session_key
							}).then(res => {
								console.log(res);
								if (res.token) {
									uni.setStorageSync('token', res.token);
									that.needToken_active = 1;
								}
								// 需要手机号并且手机号已经授权并且不需要用户信息
								if (that.needToken_active == 1 && that.needToken == 1 && that.needUserInfo == 0) {
									let pages = getCurrentPages();
									let prevPage = pages[pages.length - 2]; //上一个页面
									console.log(prevPage);
									//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
									prevPage.setData({
										id: that.id,
										isDis: that.isDis,
										uid: that.uid,
										isShare: that.isShare
									});
									wx.navigateBack({
										//返回
										delta: 1
									});
								} else if (that.needToken_active == 1 && that.needToken_active == 1 && that.needUserInfo == 1 && that.needUserInfo_active == 1) {
									// 需要手机号并且手机号已经授权并且需要用户信息并且用户信息已经授权
									let pages = getCurrentPages();
									let prevPage = pages[pages.length - 2]; //上一个页面
									console.log(prevPage);
									//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
									prevPage.setData({
										id: that.id,
										isDis: that.isDis,
										uid: that.uid,
										isShare: that.isShare
									});
									wx.navigateBack({
										//返回
										delta: 1
									});
								}
							});
						}
					});
				}
			});
		}
	}
};
